* {
  margin: 0;
  padding: 0;
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__element {
  transform-origin: bottom;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translateX(-50%);
}

.stopwatch__element--center {
  z-index: 3;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch__element--secondhand {
  z-index: 2;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite move-second;
}

.stopwatch__element--minutehand {
  z-index: 1;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite move-minute;
}

.stopwatch--sped-up .stopwatch__element--secondhand {
  animation: 10s linear infinite move-second;
}

.stopwatch--sped-up .stopwatch__element--minutehand {
  animation: 600s steps(60, end) infinite move-minute;
}

@keyframes move-second {
  0% {
    transform: translateX(-50%)rotate(0);
  }

  100% {
    transform: translateX(-50%)rotate(360deg);
  }
}

@keyframes move-minute {
  0% {
    transform: translateX(-50%)rotate(0);
  }

  100% {
    transform: translateX(-50%)rotate(360deg);
  }
}
